import "./assets/css/fieldset.css!";
import {AContainer} from "./abstract/a-container";
import {IInput} from "../input/interface/i-input";
import {IWidget} from "../widget/interface/i-widget";
import {IButton} from "../button/interface/i-button";
import {ToolBar} from "./toolbar";
import {ButtonGroup} from "./button-group";
import {ICustomComponent} from "../core/interface/i-custom-component";
import {IDataGrid} from "../datagrid/interface/i-data-grid";
import {TreeGrid} from "../treegrid/tree-grid";
import {TileList} from "../widget/tile-list";

//export type IFormBoxItem =  IInput<any>|IWidget;
export type IFieldsetItem = IWidget | IButton | IInput<any> | ToolBar | ButtonGroup | ICustomComponent | IDataGrid<any> | TreeGrid<any>;

import jquery = require("jquery");

//export type IFieldsetItem =  IInput<any>|IWidget|TileList<any>|DataGrid<any>|TreeGrid<any>;

/**
 * <img width="100%" src="../../doc_assets/fieldset.png">
 * 
 * A finalidade deste container é facilitar o trabalho com estruturas de formulários.
 * Possui métodos auxiliares que facilitam o gerenciamento dos componentes a ele adicionados, para isto
 * deve ser estendida pela classe final e cada campo que se deseje gerenciar deve ser uma propriedade da classe.
 * 
 * @export
 * @memberof module:mvcomponents/container
 * 
 * @example
 * import {FieldSet,Form} from "mvcomponents/container";
 * import {TextInput, PassWordInput} from "mvcomponents/input";
 *
 * let fieldSet = new FieldSet("login");
 * let formTest = new Form();
 * 
 * let txtLogin = new TextInput("login");
 * txtLogin.setLabel("Login");
 * txtLogin.setName("login");
 * txtLogin.setSize(12);
 * fieldSet.append(txtLogin);
 * txtSenha = new PassWordInput("senha");
 * txtSenha.setLabel("Senha");
 * txtSenha.setName("senha");
 * txtSenha.setSize(12);
 * fieldSet.append(txtSenha);
 * btnOK = new Button("OK");
 * btnOK.addEvent(EMouseEvent.CLICK, () => {
 *     console.log('logar');
 * });
 * btnOK.setSize(12);
 * 
 * formTest.append(fieldSet);
 * formTest.append(btnOK);
 * 
*/
export class Fieldset extends AContainer<IFieldsetItem> {
	private inputs:{name:string,input:IInput<any>}[];
	public isFieldset:boolean;
	constructor(legend:string) {
		super("fieldset", `<legend>${legend}</legend>`);
		this.addStyleName("Fieldset");
		this.inputs = [];
		this.isFieldset = true;
	}
	protected insertItem(element: IFieldsetItem, method: string): void {
		let input = <IInput<any>>element;
		let name:string = input.getName && input.getName();
		if (name) {
			this.inputs.push({name:name,input:input});
		}
		super.insertItem(element, method);
	}
	/**
	* Modifica o texto de título.
	* @param {string} legend
	* @returns {FieldSet}
	*/
	public setLegend(legend:string):this{
		jquery(this.element).find("legend:first").text(legend);
		return this;
	}
	public rendered(): void {
		this.onAttached.emit(null);
	}
	/**
	* Habilita/desabilita o container.
	* @param {boolean} on
	* @returns {FieldSet}
	*/
	public setEnable(on: boolean): this {
		this.addBlockContainer();
		super.setEnable(on);
		return this;
	}
}
